import React from 'react'
import $ from 'jquery';

export default props => {
  const options = ['JSON', 'YAML'];
  const samples = props.samples;
  const json = samples.find(s => s.filename === "json.md");
  const yaml = samples.find(s => s.filename === "yaml.md");
  const handler = function(e) {
    console.log(e.target);
  };
  return (
    <div>
      <div dangerouslySetInnerHTML={{ __html: json.html }} onClick={handler} />
      <div dangerouslySetInnerHTML={{ __html: yaml.html }} onClick={handler} />
    </div>
  )
}
